
body {
  background: white;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 140%;
  color: black;
  min-width: 320px;
}

.center {
  margin: 0;
  max-width: none;
}

@media(min-width: 1170px) {
  .center {
    max-width: 1170px;
    margin-right: auto;
    margin-left: auto;
    box-sizing: border-box;
  }
}


*:hover,
*:focus,
*:active {
  outline: 0;
  outline-offset: 0;
}

p {
  margin: 0;
}

.header {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  padding: 18px 15px;
  box-sizing: border-box;
}
.banner {
  background: #1F397B;
  padding: 40px 0 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.banner .center {
  width: 100%;
}

.banner_title {
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  line-height: 110%;
  color: #FFFFFF;
  margin-bottom: 25px;
  margin-right: 15px;
  margin-left: 15px;

  & span {
    color: #FFC940;
  }
}

.banner_list {
  list-style: none;
  padding-left: 0;
  margin: 0 15px;
}

.banner_item {
  font-style: normal;
  font-weight: 500;
  font-size: 17px;
  line-height: 120%;
  color: #FFFFFF;
  margin-bottom: 14px;
  padding-left: 29px;
  position: relative;

  &::before {
    content: "";
    display: inline-block;
    background: url("data:image/svg+xml,%0A%3Csvg width='19' height='13' viewBox='0 0 19 13' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 4.90191L7.6921 11L18 1' stroke='%23FFC940' stroke-width='2'/%3E%3C/svg%3E%0A") center no-repeat;
    width: 17px;
    height: 10px;
    position: absolute;
    top: calc(50% - 5px);
    left: 0;
  }
}

.banner_button {
  background: #FFC531;
  border-radius: 3px;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 140%;
  color: #000000;
  display: inline-block;
  text-align: center;
  border: none;
  text-decoration: none;
  padding: 15px 15px;
  margin: 16px 15px 30px;
  width: calc(100% - 30px);
  box-sizing: border-box;

  &:hover {
    cursor: pointer;
    background: black;
    color: #FFC531;
  }
}

.banner_img {
  width: 100%;
  position: relative;
  bottom: -5px;
}

.advantages {
  padding: 50px 15px 70px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.advantages_item {
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.advantages_sum {
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 140%;
  color: #000000;
}
.advantages_sum span {
  font-style: normal;
  font-weight: bold;
  font-size: 50px;
  line-height: 100%;
  text-transform: uppercase;
  color: #000000;
}
.advantages_description {
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  line-height: 140%;
  color: #000000;
}
.steps {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 15px 70px;
}
.steps_title {
  font-style: normal;
  font-weight: bold;
  font-size: 26px;
  line-height: 110%;
  text-align: center;
  color: #100F0F;
  margin-bottom: 30px;
}
.steps_item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.steps_item:not(:last-of-type)::after {
  content: "";
  display: block;
  background: url("data:image/svg+xml,%0A%3Csvg width='33' height='18' viewBox='0 0 33 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M32 1L16.5588 16L1.11765 0.999999' stroke='black' stroke-width='2'/%3E%3C/svg%3E%0A") center no-repeat;
  width: 15px;
  height: 30.88px;
  margin-top: 30px;
  margin-bottom: 30px;
}
.steps_img {
  margin-bottom: 18px;
}
.steps_subtitle {
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 110%;
  text-align: center;
  color: #000000;
  margin-bottom: 10px;
}
.steps_description {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 140%;
  text-align: center;
  color: #000000;
}
.form {
  display: flex;
  flex-direction: column;
  padding-bottom: 24px;
  background: #FFFFFF;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  margin-right: 15px;
  margin-left: 15px;
  margin-bottom: 100px;
}

.form_title {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 110%;
  text-align: center;
  color: #100F0F;
  margin-bottom: 10px;
  background: #FFC940;
  border-radius: 4px 4px 0px 0px;
  padding: 16px 0;
}

.form input {
  margin-top: 14px;
  padding: 15px;
  background: #F4F4F4;
  border: 1px solid #C5C5C5;
  border-radius: 4px;
  box-sizing: border-box;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 110%;
  color: #999999;
  width: calc(100% - 30px);
  box-sizing: border-box;
  margin-right: 15px;
  margin-left: 15px;

  &::placeholder {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 110%;
    color: #8A8A8A;
  }

  &[type="number"]::-webkit-outer-spin-button,
  &[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  &[type="number"] {
    -moz-appearance: textfield;
  }
}

.form_prompt {
  margin-top: 8px;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 140%;
  color: #000000;
  margin-right: 15px;
  margin-left: 15px;
}

#birthdayDate::-webkit-calendar-picker-indicator {
  display: none;
}

.form_select {
  padding: 15px;
  background: #F9F9F9;
  border: 1px solid #C5C5C5;
  box-sizing: border-box;
  margin-top: 14px;
  appearance: none;
  background: url("data:image/svg+xml,%0A%3Csvg width='14' height='8' viewBox='0 0 14 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.00023 5.17192L11.9502 0.221924L13.3642 1.63592L7.00023 7.99992L0.63623 1.63592L2.05023 0.221924L7.00023 5.17192Z' fill='black'/%3E%3C/svg%3E%0A") 98% center no-repeat;
  width: 100%;
  box-sizing: border-box;
  color: #999999;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 120%;
}

input.user-valid,
select.user-valid {
  background: url("data:image/svg+xml,%0A%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.646 14.818a.5.5 0 00.707 0l8.132-8.132A1 1 0 1119.9 8.1l-9.193 9.193a1 1 0 01-1.414 0l-4.95-4.95a1 1 0 111.414-1.414l3.89 3.89z' fill='%2315AF40'/%3E%3C/svg%3E") 98% center no-repeat;
}
input.user-invalid,
select.user-invalid {
  border-color: crimson;
}

.form .form_submit {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 140%;
  align-items: center;
  text-align: center;
  color: #000000;
  background: #FFC940;
  border-radius: 4px;
  border: none;
  padding: 18px 0;
  box-sizing: border-box;
  margin-top: 20px;

  &:hover {
    cursor: pointer;
    background: black;
    color: #FFC940;
  }
}

:active, :hover, :focus {
  outline: 0;
  outline-offset: 0;
}

.table {
  padding: 0 15px 100px;
  display: flex;
  flex-direction: column;
}

.table_title {
  font-style: normal;
  font-weight: bold;
  font-size: 26px;
  line-height: 110%;
  text-align: center;
  color: #100F0F;
  margin-bottom: 30px;
}

.table_wrapper {
  overflow-x: scroll;
  display: flex;
  flex-direction: column;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    width: 0;
  }
}

.table_row {
  display: flex;
  flex-direction: row;
  text-align: center;
  flex-shrink: 0;
  flex-grow: 1;
  width: 100%;
  
  &--title .table_cell{
    background: #FFC940;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 110%;
    color: #000000;
  }

  &--odd .table_cell{
    font-style: normal;
    font-weight: normal;
    font-size: 17px;
    line-height: 140%;
    color: #000000;
    background: white;
  }

  &--even .table_cell{
    font-style: normal;
    font-weight: normal;
    font-size: 17px;
    line-height: 140%;
    color: #000000;
    background: #F3F3F3;
  }
}

.table_cell {
  flex-grow: 1;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 15px 0;

  &:not(:last-of-type) {
    border-right: 1px solid #CFCFCF;
  }
  &:nth-of-type(1) {
    flex-basis: 132px;
  }
  &:nth-of-type(2) {
    flex-basis: 137px;
  }
  &:nth-of-type(3) {
    flex-basis: 107px;
  }
  &:nth-of-type(4) {
    flex-basis: 232px;
  }
}

.table_subtitle {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 140%;
  color: #000000;
  margin-top: 15px;
  min-width: 603px;
  display: inline-block;
}

.footer {
  padding: 50px 15px;
  background: #343532;
}

.footer_text,
.footer_link {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 140%;
  color: #B9B9B9;
  margin-bottom: 8px;
}

.footer_subtitle {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 140%;
  color: #FFFFFF;
  text-decoration: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  margin-top: 20px;
}

@media(min-width: 1170px) {
  .header {
    padding: 31px;
  }

  .banner {
    padding: 70px 0;
  }

  .banner .center {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .banner_column {
    max-width: 607px;
  }

  .banner_title {
    font-size: 45px;
    margin-bottom: 30px;
    margin-right: 0;
    margin-left: 0;
  }

  .banner_item {
    font-size: 19px;
    margin-bottom: 15px;
  }

  .banner_button {
    margin-bottom: 0;
    margin-top: 25px;
    width: auto;
    padding: 15px 43px;
  }

  .banner_img {
    width: 500px;
    height: 380px;
  }

  .advantages {
    flex-direction: row;
    justify-content: space-between;
    padding: 80px 125px 120px;
  }

  .advantages_item {
    margin-bottom: 0;
  }

  .steps {
    flex-direction: row;
    flex-wrap: wrap;
    padding: 0 125px 120px;
  }

  .steps_item {
    flex-basis: 225px;
    position: relative;

    &::after {
      position: absolute;
      right: 0;
      transform: rotate(-90deg);
      top: 11px;
      right: -45px;
    }

    &:not(:last-of-type) {
      margin-right: 120px;
    }
  }

  .steps_title {
    width: 100%;
    font-size: 40px;
    padding: 0 115px;
    margin-bottom: 50px;
  }

  .steps_img {
    margin-bottom: 27px;
  }

  .steps_subtitle {
    margin-bottom: 15px;
  }

  .form {
    align-items: center;
    padding-bottom: 40px;
    margin-bottom: 100px;
    max-width: 830px;
    margin-right: auto;
    margin-left: auto;
  }

  .form_title {
    font-size: 38px;
    padding: 25px 0;
    margin-bottom: 26px;
    width: 100%;
  }

  .form input,
  .form select  {
    width: 430px;
  }

  .form_prompt {
    min-width: 430px;
  }

  .table_title {
    font-size: 40px;
    margin-bottom: 40px;
  }
  
  .footer {
    padding: 70px 0;
  }

  .footer .center {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .footer_text,
  .footer_link {
    font-size: 14px;
  }

  .footer_subtitle {
    margin-top: 30px;
    font-size: 14px;
  }
}

.disable_page {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: white;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  line-height: 110%;
  color: #0F0E0E;

  & span {
    display: block;
    font-weight: normal;
  }

  &.active {
    display: flex;
  }
}

.loader {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background: rgba(0,0,0,.3);
  z-index: -99;
  opacity: 0;
  pointer-events: none;
  visibility: hidden;

  &::before {
    content: url(../img/loader.svg);
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }
  &.active {
    z-index: 99;
    opacity: 1;
    pointer-events: all;
    visibility: visible;
  }
}

.valid-error {
  color:  crimson;
  margin-right: 15px;
  margin-left: 15px;
}